@import 'https://fonts.googleapis.com/css?family=Roboto:400,400i,700,700i&amp;subset=cyrillic';
@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 2rem;
  --color-text-main: #000; }

* {
  padding: 0;
  margin: 0; }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

/* 
 * BrowserSync Notification */
#__bs_notify__ {
  display: none !important; }

@-webkit-keyframes navtoggleshake {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes navtoggleshake {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.5rem);
    transform: translate(0, -0.5rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.5rem);
    transform: translate(0, -0.5rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes navitemin {
  from {
    opacity: 0;
    -webkit-transform: translateX(3rem);
    transform: translateX(3rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@keyframes navitemin {
  from {
    opacity: 0;
    -webkit-transform: translateX(3rem);
    transform: translateX(3rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

html {
  font: var(--font-size-initial)/1.5 "Roboto", Helvetica, sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

.site-title,
h1, h2, h3, h4, h5, h6 {
  margin: 1em 0 0.5em 0;
  padding: 0; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em;
  font-weight: bold; }

p {
  margin: 0;
  padding-bottom: 1.5rem; }

ol, ul, dl {
  margin: 0 0 1.5rem 0; }

ul, ol {
  padding: 0;
  margin-left: 1.5rem; }

ul li, ol li {
  list-style-position: inside; }

ul ul {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem; }

ul li {
  list-style-type: square; }

ul ul li {
  list-style-type: disc; }

ul ul ul li {
  list-style-type: circle; }

blockquote {
  width: 40%;
  min-width: 15rem;
  border-left: 4px solid #DDD;
  font-style: italic;
  padding: 1rem 2rem;
  position: relative;
  color: #444;
  font-family: "Georgia", serif;
  padding-top: 0; }

blockquote .author {
  text-align: right; }

table {
  width: 100%;
  table-layout: fixed;
  margin-bottom: 1.5rem;
  border: 1px solid #CCC;
  border-spacing: 0;
  border-collapse: collapse; }

table td, table th {
  border: 1px solid #CCC;
  padding: 0.3rem 0.5rem; }

table thead th {
  text-align: left;
  background: #CCC; }

table tr:nth-child(odd) td {
  background: #F1F1F1; }

table tr:hover td {
  background: black;
  color: #FFF;
  border-color: transparent; }

hr {
  margin: 2rem 0; }

img[class^='align'] {
  padding: 0.5rem;
  background: #FFF;
  border: 3px solid #222;
  -webkit-box-shadow: 0 0 5rem 0.1rem rgba(0, 0, 0, 0.25);
  box-shadow: 0 0 5rem 0.1rem rgba(0, 0, 0, 0.25); }

.align-left {
  float: left;
  margin: 0 1.5rem 1rem 0; }

.align-right {
  float: right;
  margin: 0 0 1rem 1.5rem; }

#nav-main-toggle {
  display: none; }

.navigation {
  display: none; }

.navigation ul {
  padding: 1rem 1.5rem 2rem 1.5rem;
  margin: 0;
  background: #000;
  width: 100vw;
  color: #FFF; }

.navigation ul li {
  list-style-type: none;
  padding: 1rem 0;
  border-bottom: 0.1px solid #888;
  -webkit-animation: navitemin 0.3s 1 ease forwards;
  animation: navitemin 0.3s 1 ease forwards; }

.navigation ul li:last-child {
  border-bottom: none;
  padding-bottom: 1rem; }

.navigation ul li a {
  text-decoration: none;
  color: #FFF;
  font-size: 1rem;
  text-transform: uppercase; }

#nav-main-toggle:checked + .site-header .navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

#nav-main-toggle:checked + .site-header .nav-toggle:before {
  content: '\f00d';
  -webkit-animation: jump 0.3s 1 ease-in-out forwards;
  animation: jump 0.3s 1 ease-in-out forwards; }

.navigation ul li a:before {
  color: #FFF;
  font-family: 'Font Awesome 5 Free';
  font-weight: bold;
  display: inline-block;
  margin-right: 1rem; }

.navigation ul li:nth-child(1) a:before {
  content: '\f6f1'; }

.navigation ul li:nth-child(2) a:before {
  content: '\f129'; }

.navigation ul li:nth-child(3) a:before {
  content: '\f012'; }

.navigation ul li:nth-child(4) a:before {
  content: '\f080'; }

.navigation ul li:nth-child(5) a:before {
  content: '\f19d'; }

.navigation ul li:nth-child(6) a:before {
  content: '\f542'; }

.navigation ul li:nth-child(7) a:before {
  content: '\f573'; }

.navigation ul li a.active,
.navigation ul li a.active:before {
  color: #DF9D00; }

.navigation ul li a:hover:before,
.navigation ul li a:hover {
  color: #DF9D00; }

header.site-header div.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0.5rem;
  color: #FFF;
  background: #000; }

div.wrapper .site-title a {
  font-size: 1.5rem;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFF;
  margin-left: 1rem;
  font-weight: bold;
  -webkit-animation: navtoggleshake 0.5s 1 ease-in-out forwards;
  animation: navtoggleshake 0.5s 1 ease-in-out forwards; }

#nav-toggle {
  font-size: 1.5rem;
  margin-right: 1rem;
  font-weight: bold;
  -webkit-animation: navtoggleshake 0.5s 1 ease-in-out forwards;
  animation: navtoggleshake 0.5s 1 ease-in-out forwards; }

#nav-toggle:before {
  display: block;
  content: '\f0c9'; }

body > footer:last-of-type {
  margin-top: auto;
  background: #000;
  color: #FFF;
  padding: 1rem 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; }

body > footer:last-of-type > div {
  -webkit-animation: navtoggleshake 0.5s 1 ease-in-out forwards;
  animation: navtoggleshake 0.5s 1 ease-in-out forwards; }

body > footer:last-of-type .social a {
  margin-right: 0.7rem;
  font-size: 1.5rem;
  color: white; }

body > footer:last-of-type .social a:hover {
  color: #DF9D00; }

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100vh;
  overflow: hidden; }

@media screen and (min-width: 700px) {
  body {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between; }
  body > footer:last-of-type {
    height: 100vh;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column; }
  body > footer:last-of-type .author {
    margin-top: 40rem; }
  header.site-header {
    width: 30%; }
  #nav-toggle {
    display: none; }
  .navigation {
    display: block; }
  .navigation ul {
    width: 100%;
    height: 100vh; }
  div.wrapper .site-branding {
    background: #FFCC00;
    width: 70%;
    margin: 4rem auto;
    position: relative;
    padding: 2rem 1rem 2rem 1rem;
    border-radius: 0.5rem; }
  div.wrapper .site-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center; }
  div.wrapper .site-title a {
    color: #000; }
  div.wrapper .site-branding::after {
    position: absolute;
    bottom: -40px;
    right: 0;
    content: 'f';
    font-weight: bold;
    font-size: 5em;
    color: #000; } }
