@keyframes navtoggleshake {
    from {
        opacity: 0;
        transform: translate(0, -2rem);
    }

    to {
        opacity: 1;
        transform: translate(0, 0);
    }
}

@keyframes jump {
    0% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate(0, -0.5rem);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes navitemin {
    from {
        opacity: 0;
        transform: translateX(3rem);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}